import React, { forwardRef, useState } from "react";

import Label from "../Label/Label";
import { colors, spacingMap } from "../styleguide";
import Typography from "../Typography/Typography";
import { uuid } from "../utils";

import {
	SelectArrow, SelectLayout, SelectWrapper, StyledSelect,
} from "./Select.styles";
import { SelectProps } from "./Select.types";

const Select = forwardRef<HTMLSelectElement, SelectProps>(({
	dataQA,
	children,
	placeholder,
	label,
	required,
	labelHtmlFor,
	helperText,
	id = uuid(),
	onChange,
	disabled,
	validation,
	defaultValue,
	value,
	className,
	m,
	mb,
	ml,
	mr,
	mt,
	...props
}, ref) => {
	const selectedDefault = typeof defaultValue === "string" ? defaultValue : (placeholder || undefined);

	const [selected, setSelected] = useState<string | undefined>(selectedDefault);

	const isInvalid = validation === "invalid";

	const handleOnChange: React.ChangeEventHandler<HTMLSelectElement> = (e) => {
		setSelected(e.target.value);
		return onChange && onChange(e);
	};

	return (
		<SelectLayout m={m} mb={mb} ml={ml} mr={mr} mt={mt} className={className}>
			{label && (
				<Label
					dataQA={`${dataQA}-select-label`}
					htmlFor={labelHtmlFor}
					m={`0 0 ${spacingMap.xxs}} 0`}
					isRequired={required}
					isInvalid={isInvalid}
				>
					{label}
				</Label>
			)}
			<SelectWrapper>
				<StyledSelect
					onChange={handleOnChange}
					data-qa={dataQA}
					ref={ref}
					id={id}
					disabled={disabled}
					validation={validation}
					placeholder={placeholder}
					required={required}
					selected={value || selected}
					defaultValue={defaultValue}
					value={value}
					{...props}
				>
					{placeholder && <option value={placeholder} hidden>{placeholder}</option>}
					{children}
				</StyledSelect>
				<SelectArrow
					name="ChevronDown"
					color={disabled ? `${colors.black}4D` : undefined}
					size="20"
				/>
			</SelectWrapper>
			{helperText && (
				<Typography
					dataQA={`${dataQA}-help-text`}
					variant="caption"
					component="p"
					color={isInvalid ? colors.destructive : `${colors.black}A6`}
					m={`${spacingMap.xxs}} 0 0 0`}
				>
					{helperText}
				</Typography>
			)
			}
		</SelectLayout >
	);
});


Select.displayName = "Select";

export default Select;
